
.productDetail{
    display: flex;
    margin: 7% 4% 2%;
    border: 2px rgb(255 108 189 / 26%) solid;
    background: #ffffffad;
    border-radius: 8px;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
}
.divDetail{
    width: 100%;
}

.divDatosProd{
    width: 67%;
}
.productDetailImg{
    width: 30%;
    height: 30%;
    border-radius: 14px;
    margin-bottom: 0px;
    transition: width .3s ease-out;
}

 .productDetailImgActive{
width: 380px;
border-radius: 15px;
transition: width .6s ease-in;

}

.name{
    font-weight: 700;
    font-family: sans-serif;
    font-size: 14px;
    
}
.divBtn{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    align-content: center;
    margin-top: 30px;
    margin-bottom: 10px;
    width: 100%;
}
.btnIrCarrito{
    font-weight: 700;
    font-family: sans-serif;
    font-size: 14px;
    margin: 3%;
    color: white;
    background: rgb(230, 54, 107);
    border-radius: 5px;
    border: 1.5px red solid;
    padding: 5px 35px;
    width: max-content;
}
.btnIrCarrito:hover{
    background: rgb(134, 6, 55);
    transition: all 0.5s;
}
.btnSeguir{
    font-weight: 700;
    font-family: sans-serif;
    font-size: 14px;
    margin: 3%;
    color: white;
    background: rgb(75, 62, 255);
    border-radius: 5px;
    border: 1.5px blue solid;
    padding: 5px 20px;
    width: max-content;
}
.btnSeguir:hover{
    background: rgb(17, 17, 184);
    transition: all 0.5s;
}

.ulCategory{
    width: 100%;
    display: flex;
    justify-content: center;

}
.descrProd{
    width: 100%;
    display: flex;
    flex-direction: row;
    padding: 20px 0 20px 0;
    border: 1px #ee82ee9c solid;
    border-radius: 2px;
    place-content: center;
    transition: border .5s;
    font-weight: 700;
    font-family: sans-serif;
    font-size: 14px;

}

.categories{
    font-weight: 700;
    font-family: sans-serif;
    font-size: 14px;
margin-bottom: 5px;
}
.descrProd:hover{
    border: 1.8px rgb(77, 12, 12) solid;
    background-color: rgba(255, 192, 203, 0.801);
}

.divCategory{
    padding: 0 20px 0 20px;
    display: flex;
    flex-direction: row;
    border: 1px #ee82ee9c solid;
    border-radius: 2px;
    place-content: center;
    transition: border .5s;
    font-weight: 700;
    font-family: sans-serif;
    font-size: 14px;
    justify-content: center;
}

.divCategory:hover{
    border: 1.8px rgb(77, 12, 12) solid;
    background-color: rgba(255, 192, 203, 0.801);
}

.aviso{
    font-weight: 700;
    font-family: sans-serif;
    font-size: 14px;
}

@media screen and (max-width: 800px) {
    .productDetailImg{
        width: 250px;
        transition: width .6s ease-in;

    }
 .productDetailImgActive{
    width: 260px;
    border-radius: 15px;
transition: width .6s ease-in;
    }

    .btnIrCarrito{
        
        padding: 3px 7%;
        width: max-content;
    }
    .btnSeguir{
    
        padding: 3px 5%;
        width: max-content;
    }
    .divDatosProd{
        width: 100%;
    }
    
}